v></div> <div class=\"container-fluid\" data-v-57018884><header class=\"section-header\" data-v-57018884><h3 data-v-57018884>Belajar bersama kami</h3> <p data-v-57018884>Aplikasi Bimbel Pertama Berbasis Multiple Intelligence</p></header> <div class=\"row\" data-v-57018884><div class=\"col-lg-6\" data-v-57018884><div class=\"why-us-img\" data-v-57018884><img src=\"https://res.cloudinary.com/douzspxoy/image/upload/v1626922071/images/landingpage/sanedumaskot_zwdxi8.jpg\" alt class=\"img-fluid\" data-v-57018884></div></div> <div class=\"col-lg-6\" data-v-57018884><div class=\"why-us-content\" data-v-57018884><p data-v-57018884>Sanedu hadir sebagi asisten belajar siswa yang memberikan layanan perencanaan dan pendampingan belajar dengan menggunakan metode multiple intellegence system</p> <p data-v-57018884>Tidak ada siswa bodoh, yang ada hanyalah siswa yang belum mengerti</p> <p data-v-57018884>Pendidikan bukan hanya tentang menjawab soal, namun bagaimana siswa dapat memahami kegunaan dari materi yang diajarkan bagi kehidupannya</p> <p data-v-57018884>Tujuan pendidikan bukan membuat nilai ujian besar, namun membuat siswa paham tentang apa yang mereka pelajari</p> <div class=\"features wow bounceInUp clearfix\" data-v-57018884><i class=\"fa fa-diamond\" style=\"color: #f058dc;\" data-v-57018884></i> <h4 data-v-57018884>Cara mudah dan cepat paham</h4></div> <div class=\"features wow bounceInUp clearfix\" data-v-57018884><i class=\"fa fa-book\" style=\"color: #ffb774;\" data-v-57018884></i> <h4 data-v-57018884>Bimbingan belajar kekinian</h4></div> <div class=\"features wow bounceInUp clearfix\" data-v-57018884><i class=\"fa fa-object-group\" style=\"color: #c4b955;\" data-v-57018884></i> <h4 data-v-57018884>Pasti bisa jawab semua soal</h4></div> <div class=\"features wow bounceInUp clearfix\" data-v-57018884><i class=\"fa fa-language\" style=\"color: #589af1;\" data-v-57018884></i> <h4 data-v-57018884>Belajar tanpa beban</h4></div></div></div></div></div></div> <div id=\"services\" class=\"section-bg\" data-v-57018884><div class=\"container\" data-v-57018884><header class=\"section-header\" data-v-57018884><h3 data-v-57018884>Fitur</h3></header> <div class=\"row\" data-v-57018884><div data-wow-duration=\"1.4s\" class=\"col-md-6 col-lg-4 wow bounceInUp\" data-v-57018884><div class=\"box\" data-v-57018884><div class=\"icon\" style=\"background: #fceef3;\" data-v-57018884><i class=\"ion-ios-paper-outline\" style=\"color: #ff689b;\" data-v-57018884></i></div> <h4 class=\"title\" data-v-57018884><a href data-v-57018884>Etalase Soal</a></h4> <p class=\"description\" data-v-57018884>Melalui fitur ini, kamu dapat melatih kemampuan kamu dalam mengejerjakan soal-soal mulai dari yang mudah hingga yang sulit dilengkapi dengan analisis IRT dan pembahasannya.</p></div></div> <div data-wow-duration=\"1.4s\" class=\"col-md-6 col-lg-4 wow bounceInUp\" data-v-57018884><div class=\"box\" data-v-57018884><div class=\"icon\" style=\"background: #fff0da;\" data-v-57018884><i class=\"ion-ios-bookmarks-outline\" style=\"color: #e98e06;\" data-v-57018884></i></div> <h4 class=\"title\" data-v-57018884><a href data-v-57018884>San Project</a></h4> <p class=\"description\" data-v-57018884>Fitur yang satu ini menyediakan program-program persiapan ujian dan pelatihan kemampuan siswa,seperti tryout, seminar motivasi, workshop minat bakat dan education camp.</p></div></div> <div data-wow-delay=\"0.1s\" data-wow-duration=\"1.4s\" class=\"col-md-6 col-lg-4 wow bounceInUp\" data-v-57018884><div class=\"box\" data-v-57018884><div class=\"icon\" style=\"background: #e6fdfc;\" data-v-57018884><i class=\"ion-ios-calculator-outline\" style=\"color: #3fcdc7;\" data-v-57018884></i></div> <h4 class=\"title\" data-v-57018884><a href data-v-57018884>San Hitung</a></h4> <p class=\"description\" data-v-57018884>Fitur ini digunakan untuk menghitung berbagai potensi diri kamu, mulai dari minat dan bakat kamu, kecenderungan kecerdasan hingga potensi kuliah dan karir kamu.</p></div></di